import React, { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import { auth, signInWithGoogle } from "../config/firebase";
import { useAuthState } from "react-firebase-hooks/auth";

const Login = () => {
    // Use the useAuthState hook to track the authentication state (user, loading, error)
    const [user, loading, error] = useAuthState(auth);

    // Use the useNavigate hook from React Router to programmatically navigate to different routes
    const navigate = useNavigate();

    // useEffect to handle navigation based on the authentication state
    useEffect(() => {
        // If still loading, do nothing
        if (loading) {
            return;
        }

        // If the user is authenticated, navigate to the home page
        if (user) {
            navigate("/home");
        }
    }, [user, loading]);

    return (
        <>
            {/* Login page content */}
            <div id="loginpage">
                <div id="logocontainer">
                    <div id="loginform">
                        {/* Google Sign-In button */}
                        <button className="gsi-material-button" onClick={signInWithGoogle}>
                            {/* Google Sign-In button content */}
                            <div className="gsi-material-button-state"></div>
                            <div className="gsi-material-button-content-wrapper">
                                <div className="gsi-material-button-icon">
                                {/* Google Sign-In button SVG content */}
                                <svg version="1.1" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 48 48" xmlnsXlink="http://www.w3.org/1999/xlink" style={{display: 'block'}}>        
                                        {/* ... SVG paths for Google logo */}
                                        <path fill="#EA4335" d="M24 9.5c3.54 0 6.71 1.22 9.21 3.6l6.85-6.85C35.9 2.38 30.47 0 24 0 14.62 0 6.51 5.38 2.56 13.22l7.98 6.19C12.43 13.72 17.74 9.5 24 9.5z"></path>
                                        <path fill="#4285F4" d="M46.98 24.55c0-1.57-.15-3.09-.38-4.55H24v9.02h12.94c-.58 2.96-2.26 5.48-4.78 7.18l7.73 6c4.51-4.18 7.09-10.36 7.09-17.65z"></path>
                                        <path fill="#FBBC05" d="M10.53 28.59c-.48-1.45-.76-2.99-.76-4.59s.27-3.14.76-4.59l-7.98-6.19C.92 16.46 0 20.12 0 24c0 3.88.92 7.54 2.56 10.78l7.97-6.19z"></path>
                                        <path fill="#34A853" d="M24 48c6.48 0 11.93-2.13 15.89-5.81l-7.73-6c-2.15 1.45-4.92 2.3-8.16 2.3-6.26 0-11.57-4.22-13.47-9.91l-7.98 6.19C6.51 42.62 14.62 48 24 48z"></path>
                                        <path fill="none" d="M0 0h48v48H0z"></path>
                                    </svg>
                                </div>
                                {/* Text content for the Google Sign-In button */}
                                <span className="gsi-material-button-contents">Sign in with Google</span>
                                {/* Hidden text for accessibility */}
                                <span style={{ display: 'none' }}>Sign in with Google</span>
                            </div>
                        </button>
                    </div>
                </div>

                {/* Footer section */}
                <div id="footer">
                    {/* Link to GitHub profile */}
                    <a href="https://github.com/OreYusuf/" target="_blank">
                        <p id="createdBy">Created By Ore Yusuf</p>
                    </a>
                </div>
            </div>
        </>
    );
};

// Export the Login component for use in other parts of the application
export default Login;
